/*large button*/
.button-primary-lg-1 {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 16px 24px;

    /* Test Color/50 */

    background: #c5cae9;
    border-color: transparent;
    color: white;
    /* Shadow 1 */

    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.12);
    border-radius: 6px;
}

.button-primary-lg-1:hover {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 16px 24px;
    border-color: transparent;
    color: white;
    background: #9fa8da;
    /* Shadow 1 */

    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.12);
    border-radius: 6px;
}

