.h-100 {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;
}

.margin-inside {
    flex: none;
    order: 0;
    flex-grow: 0;
    margin-top: 200px;
}

.margin-80 {
    margin-bottom: 80px;
}

.margin-16 {
    margin: 16px;
}

.margin-center {
    display: grid;
    place-items: center;

    padding: 0px;
    height: 100vh;
}

.width-764 {
    width: 764px;
}

.image-border {
    border: 2px solid transparent;

    border-radius: 24px;
}

.image-border:hover {
    border: 2px solid #d6d6ff;

    border-radius: 24px;
}
